@use 'base/variables'as v;
@use 'base/mixins'as m;

.contenedor-login{
    
    @include m.tablet{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        min-height: 100vh;

    }
}

.imagen-login{
    height: 20rem;
    background-image: url('/build/img/Fondo1.jpg');
    background-size: cover;
    background-position: center;
    @include m.tablet{
        height: auto;
    }
}


