    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  justify-content: space-between;
  text-decoration: none;
  font-weight: 700;
  display: block;
  font-size: 2.5rem;
  margin: 2rem;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
}
.navegacion a:hover {
  color: #e6e6e6;
}

.formulario {
  background-color: #e1e1e1;
  max-width: 100%;
}
@media (min-width: 768px) {
  .formulario {
    width: 50%;
    margin-top: 5rem;
    margin-left: auto;
    margin-right: auto;
  }
}
.formulario .label-desplegable, .formulario select {
  margin-left: -15rem;
}
.formulario .label-categoria {
  margin-left: -11rem;
}
.formulario .label-categoria select {
  margin-left: -5rem;
}

.campo-categoria {
  margin-top: 3rem;
  margin-left: 35.5rem;
  margin-bottom: 1rem;
}

.campo-provincia {
  margin-top: 3rem;
  margin-bottom: 1rem;
  margin-left: 14rem;
}

.navegacionPaginacion .pagination {
  display: inline-block;
  padding: 0;
  margin: 0;
  margin-left: 45rem;
  margin-top: 2.5rem;
}
.navegacionPaginacion .pagination .page-item {
  display: inline;
}
.navegacionPaginacion .pagination .page-item a {
  background-color: #FFFFFF;
  color: black;
  float: left;
  padding: 8px 16px;
  text-decoration: none;
  border-right: 1px solid #1a1b15;
}
.navegacionPaginacion .pagination .page-item a:hover {
  background-color: #e6e6e6;
  cursor: pointer;
}
.navegacionPaginacion .ocultar {
  display: none;
}